aside.au-side-nav {
  display: none;
}
@include from (portrait) {
  aside.au-side-nav {
    display: block;
    border: 1px solid #ddd;
    text-align: center;
    box-shadow: 0px 1px 8px rgba(0, 0, 0, 0.4);
    min-height: 300px;
    &.fixed {
      position: fixed;
      width: 250px;
      @include from (landscape) {
        width: 310px;
      }
      top: 2rem;
      &.bottom {
          position: absolute;
          top: auto;
          bottom: 0;
      }
    }
  }
}
