section#home-packages {
    text-align: center;
    background-color: #bdb698;
    background-color: rgba(189, 182, 152, 0.5);
    margin-top: -231px;
    position: relative;
    overflow: hidden;
    padding-top: 2rem;

    &.in-content {
      margin-top: 3rem;
    }

    h2 {
      margin-bottom: 2rem;
    }

    .cards-container {
      max-width: 1000px;
    }
}

.cards-bg {
  background: url(../assets/img/card-bg.png);
  background-size: 100%;
  background-size: cover;
  background-position: center center;
  position: absolute;
  top: 232px;
  left: 0;
  width: 100%;
  height: 100%;
}

.card-container {
    display: block;
    background: #fff;
    box-shadow: 0px 1px 8px rgba(0, 0, 0, 0.4);
    @include to (landscape) {
      margin-bottom: 2rem;
    }

    .card-head {
      background: #242424;
      color: #fff;
      h3 {
        display: block;

        a {
          color: #fff;
          background: #000;
          text-decoration: none;
          display: block;
          padding: 0.5rem 1rem;
          &:hover {
            text-decoration: underline;
          }
        }
      }
      p {
        margin: 0;
        padding: 0.75rem 1rem;
        font-size: 1.2rem;
      }
    }

    .card-body {
      @include from (landscape) {
        min-height: 440px;
      }

      ul {
        margin-left: 0;
        padding: 1rem;
          li {
          list-style: none;
          padding: 0;
        }
      }
    }

    .card-foot {
      display: block;
      padding: 1rem;

      .card-price {
        padding: 1rem;
        display: block;
        background: #ddd;
        border: 1px solid #bbb;

        p.price-from {
          display: block;
          margin: 0px auto;
          text-transform: uppercase;
          letter-spacing: 3px;
          font-weight: 700;
        }

        p.price {
          font-weight: 400;
          font-size: 4rem;
          position: relative;
          line-height: 1;
          margin: 0px auto;

          span.dolar {
            font-size: 1.5rem;
            position: relative;
            top: -1.5rem;
            margin-left: 2rem;
          }
          span.per-person {
            font-size: 0.85rem;
            max-width: 50px;
            text-align: left;
            display: inline-block;
          }
        }
      }
      .au-btn {
        font-size: 0.8rem;
        width: 45%;
        padding: 0.5rem;
        letter-spacing: 1.5px;
        display: inline-block;
        border: 1px solid #000;
        &.btn-white {
          margin-right:5%;
        }
      }
    }
}
