body {
  margin: 0;
}
section.au-body {
  padding: 3rem 0;

  * > .au-btn {
    margin-top: 1rem;
  }

  .au-card-list li {
    margin: 1rem auto 0.5rem;
  }
}

@include from (portrait) {
  aside.au-side-nav.au-accordion {
      max-width: 250px;
  }
}

section.au-body .au-card-list li {
    margin: 1rem 0 0.5rem;
}

.au-table__wrapper {
    margin-top: 1.5rem;
}

.au-btn, section.au-body * > .au-btn {
  border-radius: 0;
  border: none;
  background: #000;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 3px;
  font-weight: 700;
  padding: 0.5rem 1rem;

  &.btn-white {
    background: #fff;
    color: #000;
    border: 1px solid #000;
  }
}
