section#hero-banner {
  padding: 0;
  &.page-title .background-image {
    height: 220px;
    overflow: hidden;
  }
  .background-image {
      @include from (portrait) {
        height: auto;
      }
      width: 100%;
      background-size: cover;
      background-position: center center;
      text-align: center;
      position: relative;
      overflow: hidden;

      .background-overlay {
          background: rgba(0, 0, 0, 0.5);
          width: 100%;
          height: 100%;

          .container {
            padding: 8rem 0 18rem;
            @include from (portrait) {
              padding: 18rem 0 22rem;
            }
            max-width: 720px;
          }
      }

      h1 {
        color: #fff;
      }

      .sub-title {
        max-width: 100%;
        color: #fff;
        margin: 0.5rem 0 .25rem;
        line-height: 1.75;
        font-size: 1.15rem;

        a {
          color: #fff;

          &:hover {
            color: #aaa;
            text-decoration: none;
          }
        }
      }
    }
}
